import React, { Component } from 'react';
import '../styles/tvshow-app.scss';
import ShowTitle from './show-title';
import ShowData from './show-data';
import SeasonsSelect from './season-select';

//generic function to get API info
var loadAPIInfo = (callback, data) => {   
  var xobj = new XMLHttpRequest();
      xobj.overrideMimeType("application/json");
  xobj.open('GET', data, true);
  xobj.onreadystatechange = () =>{
    if(xobj.readyState === 4 && xobj.status === 200){
          callback(xobj.responseText);
    }
  };
  xobj.send(null);  
}

class TvShowApp extends Component {

  state = {
    showData: [],
    EpsData: [],
    Background: ''
  }

  //gets info from the API's from the urls
  getShowInfo = () =>{
    loadAPIInfo((response) => {
      var dados = JSON.parse(response);
      this.setState({showData: dados, Background: dados.Images.Background});
     }, "https://sample-api-78c77.firebaseio.com/tv-shows/SHOW123.json");

     loadAPIInfo((response) => {
      var dados = JSON.parse(response);
      this.setState({EpsData: dados});
   }, "https://sample-api-78c77.firebaseio.com/episodes/SHOW123.json");
  }

  constructor(props) {
    super(props);
    this.getShowInfo();
  }

  render() {
    return (
      <div className='page-container'>
        <div className="background">        
          <img className="background-image" alt='background' src={this.state.Background}/>
          <div className="image-shadow"></div>
        </div>  
        <ShowTitle showInfo={this.state.showData} /> 
        <SeasonsSelect epsInfo={this.state.EpsData} />     
        <ShowData showInfo={this.state.showData} />
        <div className="close-page-icon">X</div>
      </div>
    );
  }
}

export default TvShowApp;
