import React, { Component } from 'react';
import '../styles/season-select.scss';
import SeasonTab from './season-tab';
import Episode from './episode';

class SeasonsSelect extends Component {

state = {
    seasonSelected: 1,
    epSelected: 0
}

//change to the selected season
changeSeason = (nSeason) =>{
    this.setState({seasonSelected: nSeason});   
    this.setState({epSelected: 0});  
}

//check how many seasons the show has
checkQuantityOfSeasons = (epsInfo) =>{
    var seasons = [];
    epsInfo.forEach(function (ep) {
        if(ep){
            seasons.push(ep.SeasonNumber);
        }
        });
    return Math.max(...seasons); 
}

//function called by the Episode component to change the selected episode
showOrHideEPInfo = (nEp) =>{
    if(nEp === this.state.epSelected){
        this.setState({epSelected: 0});   
    }else{
        this.setState({epSelected: nEp});   
    } 
}

render() {
    return (
        <div className="container-seasons">
            <div className="season-tabs">           
                {(() =>{                
                    var seasonsQuantity = this.checkQuantityOfSeasons(this.props.epsInfo);
                    if(seasonsQuantity > 0){
                        var seasonsTabs = [];
                        for(var season = 1; season <= seasonsQuantity; season++){
                            var classTab = "season-tab";
                            if(season === this.state.seasonSelected){
                                classTab = "season-tab-selected";
                            }
                            seasonsTabs.push(<SeasonTab key={season} classTab={classTab} seasonNumber={season} changeSeason={this.changeSeason} titleTab={"T" + season} />)
                        }
                        return seasonsTabs;
                    }
                })()}
            </div>
            <div className="divider"></div>
            <div className="episodes">
                {this.props.epsInfo.map(ep => {
                    if(ep){
                        if(ep.SeasonNumber === this.state.seasonSelected){
                            var open = false;
                            if(this.state.epSelected === ep.EpisodeNumber){
                                open = true;
                            }
                            return <Episode key={ep.ID} open={open} title={ep.Title} changeEp={this.showOrHideEPInfo} synopsis={ep.Synopsis} number={ep.EpisodeNumber} image={ep.Image}/>
                        }
                    }
                })}
            </div>
        </div>
    );
  }
}

export default SeasonsSelect;
