import React, { Component } from 'react';
import '../styles/show-general.scss';
import AddToList from '../icons/add-gray-s.svg';
import Evaluate from '../icons/sad-gray-w.svg';
import Record from '../icons/rec-gray-s.svg';
import Share from '../icons/share-gray-s.svg';

class General extends Component {

  render() {
    return (
    <div className="container-general">
        <div className="icons">
            <div className="icon">
                <div className="icon-image"><img alt="button icon" src={AddToList}/></div>
                <div className="icon-text">Add to List</div>
            </div>
            <div className="icon">
                <div className="icon-image"><img alt="button icon" src={Evaluate}/></div>
                <div className="icon-text">Rate</div>
            </div>
            <div className="icon">
                <div className="icon-image"><img alt="button icon" src={Record}/></div>
                <div className="icon-text">Record</div>
            </div>
            <div className="icon">
                <div className="icon-image"><img alt="button icon" src={Share}/></div>
                <div className="icon-text">Share</div>
            </div>
        </div>
        <div className="synopsis">
            <div className="synopsis-text">
            <span className="synopsis-title">SYNOPSIS</span>
            <br />
            {this.props.showSynopsis}
            </div>
        </div>
    </div>
    );
  }
}

export default General;
