import React, { Component } from 'react';
import '../styles/season-tab.scss';

class SeasonTab extends Component {
//change the season selected on component SeasonSelect
changeClass = () =>{
this.props.changeSeason(this.props.seasonNumber);
}

  render() {
    return ( 
    <div onClick={this.changeClass} className={this.props.classTab}>
        {this.props.titleTab}
    </div>
    );
  }
}

export default SeasonTab;
