import React, { Component } from 'react';
import '../styles/show-cast.scss';

class Cast extends Component {

  render() {
    return (
    <div className="container-cast">
        <div className="cast">
            {//show the cast members on the cast tab
              this.props.cast.map(castmember => {
                        return <div className="cast-member" key={castmember.ID}><div className="member-name">{castmember.Name}</div></div>
            })}
        </div>
    </div>
    );
  }
}

export default Cast;
