import React, { Component } from 'react';
import General from './show-general';
import Cast from './show-cast';
import '../styles/show-data.scss';
import Telecine from '../icons/logo-telecine.svg';

class ShowData extends Component {

  state = {
    tabGeneralClass: "tab-selected",
    tabCastClass: "tab"
  }

  //change tab between General and Cast 
  changeTab = (tab) =>{
    var generalTab = "tab";
    var castTab = "tab";
    if(tab === "general"){
        generalTab = "tab-selected";
    }else if(tab === "cast"){
        castTab = "tab-selected";
    }
    this.setState({tabGeneralClass: generalTab, tabCastClass: castTab});
  }

  render() {
    return (
    <div className="data-area">
      <div className="tabs">
        <div className={this.state.tabGeneralClass} onClick={()=>{this.changeTab("general")}}>GENERAL</div>
        <div className={this.state.tabCastClass} onClick={()=>{this.changeTab("cast")}}>CAST</div>
      </div>
      <img className="telecine-img" alt="telecine-img" src={Telecine}/>
      <div className="divider"></div>
      <div className="tab-content">
        {//shows the info of the tab selected
          (() =>{if(this.state.tabGeneralClass === "tab-selected"){
            return <General showSynopsis={this.props.showInfo.Synopsis}/>
          }else{
            return <Cast cast={this.props.showInfo.Cast} />
          }
        })()}
      </div>
    </div>
    );
  }
}

export default ShowData;
