import React, { Component } from 'react';
import '../styles/show-title.scss';

class ShowTitle extends Component {

  //define the data to the correct format
  getTilteInfo = () =>{
   const genres = this.props.showInfo.Genres;
   const showYear = this.props.showInfo.Year;
   var titleGenres = '';
   if(genres !== undefined){
        for(var genre = 0; genre < genres.length; genre++){
            titleGenres += genres[genre].Title + " / ";
        }
        return  titleGenres + showYear;
    }
  }

  render() {
    return (
    <div className="title-area">
        <div className="title">{this.props.showInfo.Title}</div>
        <div className="title-info">{this.getTilteInfo()}</div>
    </div>
    );
  }
}

export default ShowTitle;
