import React, { Component } from 'react';
import '../styles/episode.scss';
import Play from '../icons/play-small-player-w.svg'

class Episode extends Component {

  render() {
    return ( 
    <div onClick={()=>{this.props.changeEp(this.props.number)}} className="episode-container">
        <div className="episode-title">{this.props.number} {this.props.title}
            <img className="play-button" alt="play button" src={Play}/>
        </div>
        {//clincking on the episode, the function "changeEP above, will show or hide the episode synopsis and imagee"    
        (() =>{
            if(this.props.open){
                return (
                    <div className="ep-data">
                        <img className="ep-thumbnail" alt="thumbnail" src={this.props.image}/>
                        <div className="ep-synopsis">{this.props.synopsis}</div>
                    </div>
                )
            }
        })()}
    </div>
    );
  }
}

export default Episode;
